"use client"

import React, { useState } from 'react'
import { ChevronLeft, ChevronRight, Search, Minimize2, Square, X } from 'lucide-react'

const menuItems = [
  {
    label: 'File',
    submenu: [
      { label: 'New Text File', shortcut: 'Ctrl+N' },
      { label: 'New File...', shortcut: 'Ctrl+Alt+N' },
      { label: 'New Window', shortcut: 'Ctrl+Shift+N' },
      { label: 'Open File...', shortcut: 'Ctrl+O' },
      { label: 'Open Folder...', shortcut: 'Ctrl+K Ctrl+O' },
      { label: 'Open Workspace from File...' },
      { label: 'Open Recent', submenu: [
        { label: 'Recent File 1' },
        { label: 'Recent File 2' },
        { label: 'Recent File 3' },
        { label: 'Clear Recent Files' },
      ] },
      { label: 'Add Folder to Workspace...' },
      { label: 'Save Workspace As...' },
      { label: 'Duplicate Workspace' },
      { label: 'Close Editor', shortcut: 'Ctrl+W' },
      { label: 'Exit', shortcut: 'Alt+F4' },
    ],
  },
  {
    label: 'Edit',
    submenu: [
      { label: 'Undo', shortcut: 'Ctrl+Z' },
      { label: 'Redo', shortcut: 'Ctrl+Y' },
      { label: 'Cut', shortcut: 'Ctrl+X' },
      { label: 'Copy', shortcut: 'Ctrl+C' },
      { label: 'Paste', shortcut: 'Ctrl+V' },
      { label: 'Select All', shortcut: 'Ctrl+A' },
      { label: 'Find...', shortcut: 'Ctrl+F' },
      { label: 'Replace...', shortcut: 'Ctrl+H' },
      { label: 'Go to Line...', shortcut: 'Ctrl+G' },
    ],
  },
  {
    label: 'Selection',
    submenu: [
      { label: 'Select All', shortcut: 'Ctrl+A' },
      { label: 'Select Current Line' },
      { label: 'Select Next Line' },
      { label: 'Select Previous Line' },
      { label: 'Expand Selection' },
    ],
  },
  {
    label: 'View',
    submenu: [
      { label: 'Toggle Sidebar', shortcut: 'Ctrl+B' },
      { label: 'Toggle Terminal', shortcut: 'Ctrl+`' },
      { label: 'Zoom In', shortcut: 'Ctrl+=' },
      { label: 'Zoom Out', shortcut: 'Ctrl+-' },
      { label: 'Reset Zoom', shortcut: 'Ctrl+0' },
      { label: 'Show Active Editor', shortcut: 'Ctrl+1' },
      { label: 'Show Next Editor', shortcut: 'Ctrl+Tab' },
      { label: 'Show Previous Editor', shortcut: 'Ctrl+Shift+Tab' },
    ],
  },
  {
    label: 'Go',
    submenu: [
      { label: 'Back', shortcut: 'Ctrl+-' },
      { label: 'Forward', shortcut: 'Ctrl+Shift+-' },
      { label: 'Navigate to Symbol' },
      { label: 'Navigate to File' },
      { label: 'Show All Symbols', shortcut: 'Ctrl+T' },
    ],
  },
  {
    label: 'Run',
    submenu: [
      { label: 'Run Without Debugging', shortcut: 'Ctrl+F5' },
      { label: 'Start Debugging', shortcut: 'F5' },
      { label: 'Stop Debugging', shortcut: 'Shift+F5' },
      { label: 'Debug Console' },
      { label: 'Run Task...' },
    ],
  },
  {
    label: 'Terminal',
    submenu: [
      { label: 'New Terminal', shortcut: 'Ctrl+Shift+`' },
      { label: 'Run Active File in Terminal', shortcut: 'Ctrl+Shift+P' },
      { label: 'Kill Terminal', shortcut: 'Ctrl+Shift+K' },
      { label: 'Split Terminal', shortcut: 'Ctrl+Shift+5' },
      { label: 'Show Integrated Terminal', shortcut: 'Ctrl+`' },
    ],
  },
  {
    label: 'Help',
    submenu: [
      { label: 'Documentation', shortcut: 'F1' },
      { label: 'Keyboard Shortcuts', shortcut: 'Ctrl+K Ctrl+S' },
      { label: 'Report Issue' },
      { label: 'Check for Updates' },
      { label: 'About' },
    ],
  },
];


export default function Header() {
  const [activeMenu, setActiveMenu] = useState(null)

  return (
    <div className="flex flex-col w-full text-sm">
      <div className="flex items-center bg-[#3c3c3c] text-white p-1">
        <div className="flex items-center space-x-2 mr-4">
          <img src="https://icons.veryicon.com/png/o/business/vscode-program-item-icon/vscode.png" alt="VS Code" className="w-6 h-6" />
          <div className="flex space-x-4">
            {menuItems.map((item, index) => (
              <div
                key={index}
                className="relative"
                onMouseEnter={() => setActiveMenu(index)}
                onMouseLeave={() => setActiveMenu(null)}
              >
                <span className="cursor-pointer px-2 py-1 hover:bg-[#505050]">{item.label}</span>
                {activeMenu === index && item.submenu && (
                  <div className="absolute left-0 top-full mt-1 bg-[#252526] border border-[#454545] shadow-lg z-10">
                    {item.submenu.map((subItem, subIndex) => (
                      <div key={subIndex} className="px-4 py-1 hover:bg-[#094771] flex justify-between items-center">
                        <span>{subItem.label}</span>
                        {subItem.shortcut && <span className="ml-8 text-[#8c8c8c]">{subItem.shortcut}</span>}
                      </div>
                    ))}
                  </div>
                )}
              </div>
            ))}
          </div>
        </div>
        <div className="flex-grow">
          <div className="flex items-center justify-center">
            <ChevronLeft className="w-4 h-4 mx-1 text-gray-400" />
            <ChevronRight className="w-4 h-4 mx-1 text-gray-400" />
            <div className="mx-2 px-2 py-1 bg-[#3c3c3c] border border-[#6b6b6b] rounded flex items-center">
              <Search className="w-4 h-4 mr-2 text-gray-400" />
              <input
                type="text"
                placeholder="React"
                className="bg-transparent outline-none text-white w-64"
              />
            </div>
          </div>
        </div>
        <div className="flex items-center space-x-4">
          <Minimize2 className="w-4 h-4" />
          <Square className="w-4 h-4" />
          <X className="w-4 h-4" />
        </div>
      </div>
    </div>
  )
}