"use client"

import React, { useState, useEffect, useRef } from 'react'
import { X, Play, Split, Maximize2 } from 'lucide-react'
import Prism from 'prismjs'
import 'prismjs/themes/prism-tomorrow.css'
import 'prismjs/components/prism-javascript'
import 'prismjs/components/prism-jsx'
import 'prismjs/components/prism-css'
import 'prismjs/components/prism-markdown'

const suggestions = {
    javascript: [
        'function myFunction() {}',
        'const variable = value;',
        'let variable = value;',
        'var variable = value;',
        'if (condition) {}',
        'else {}',
        'for (let i = 0; i < limit; i++) {}',
        'while (condition) {}',
        'return value;',
        'class MyClass {}',
        'import module from "module";',
        'export default MyClass;',
        'async function myAsyncFunction() {}',
        'await myAsyncFunction();',
        'try {} catch (error) {}',
        'switch (expression) { case value: break; default: }',
        'typeof variable',
        'Array.isArray(arr)',
        'JSON.stringify(object)',
        'JSON.parse(string)',
        'new Promise((resolve, reject) => {})',
        'console.log(variable)',
        'Object.keys(object)',
        'Object.values(object)',
        'Object.entries(object)',
        'map((item) => {})',
        'filter((item) => {})',
        'reduce((accumulator, current) => accumulator + current, initialValue)',
        'setTimeout(() => {}, milliseconds)',
        'setInterval(() => {}, milliseconds)',
        'clearTimeout(timeoutId)',
        'clearInterval(intervalId)',
    ],
    html: [
        '<div></div>',
        '<span></span>',
        '<p></p>',
        '<a href="#"></a>',
        '<img src="" alt=""/>',
        '<ul></ul>',
        '<li></li>',
        '<table></table>',
        '<thead></thead>',
        '<tbody></tbody>',
        '<tr></tr>',
        '<th></th>',
        '<td></td>',
        '<form></form>',
        '<input type="text"/>',
        '<button></button>',
        '<select></select>',
        '<option></option>',
        '<textarea></textarea>',
        '<label></label>',
        '<h1></h1>',
        '<h2></h2>',
        '<h3></h3>',
        '<h4></h4>',
        '<h5></h5>',
        '<h6></h6>',
        '<header></header>',
        '<footer></footer>',
        '<section></section>',
        '<article></article>',
        '<nav></nav>',
        '<aside></aside>',
        '<iframe src=""></iframe>',
        '<script></script>',
        '<style></style>',
        '<link rel="stylesheet" href=""/>',
        '<meta charset="UTF-8"/>',
        '<video></video>',
        '<audio></audio>',
        '<source src="" type=""/>',
        '<figure></figure>',
        '<figcaption></figcaption>',
        '<blockquote></blockquote>',
        '<pre></pre>',
        '<code></code>',
        '<strong></strong>',
        '<em></em>',
        '<br/>',
        '<hr/>',
        '<input type="checkbox"/>',
        '<input type="radio"/>',
        '<button type="submit"></button>',
        '<ol></ol>',
        '<dl></dl>',
        '<dt></dt>',
        '<dd></dd>',
        '<fieldset></fieldset>',
        '<legend></legend>',
        '<canvas></canvas>',
        '<svg></svg>',
        '<progress value="" max=""></progress>'
    ],
    css: [
        'color: #000;',
        'background-color: #fff;',
        'font-size: 16px;',
        'margin: 10px;',
        'padding: 10px;',
        'display: block;',
        'display: flex;',
        'display: grid;',
        'position: relative;',
        'position: absolute;',
        'position: fixed;',
        'flex-direction: row;',
        'flex-direction: column;',
        'justify-content: center;',
        'align-items: center;',
        'grid-template-columns: repeat(3, 1fr);',
        'grid-template-rows: auto;',
        'width: 100%;',
        'height: auto;',
        'max-width: 100%;',
        'min-height: 100vh;',
        'border: 1px solid #000;',
        'border-radius: 5px;',
        'box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);',
        'opacity: 0.5;',
        'visibility: hidden;',
        'z-index: 10;',
        'overflow: hidden;',
        'text-align: center;',
        'vertical-align: middle;',
        'line-height: 1.5;',
        'font-family: Arial, sans-serif;',
        'font-weight: bold;',
        'cursor: pointer;',
        'transition: all 0.3s ease;',
        'transform: rotate(45deg);'
    ],
    markdown: ['# ', '## ', '### ', '- ', '1. ', '**', '*', '```', '[]()', '![]()', '> '],
}

export default function Editor({ files, onCloseFile, onUpdateFile }) {
    const [activeFile, setActiveFile] = useState(null)
    const [openFiles, setOpenFiles] = useState([])
    const [editableContent, setEditableContent] = useState('')
    const [currentSuggestions, setCurrentSuggestions] = useState([])
    const [cursorPosition, setCursorPosition] = useState({ top: 0, left: 0 })
    const textareaRef = useRef(null)
    const highlightedLinesRef = useRef(null)
    const lineNumbersRef = useRef(null)

    useEffect(() => {
        if (files && files.length > 0) {
            const newOpenFiles = files.filter(file => !openFiles.some(f => f.name === file.name))
            setOpenFiles([...openFiles, ...newOpenFiles])
            if (!activeFile) {
                setActiveFile(files[0])
                setEditableContent(files[0].content)
            }
        }
    }, [files])

    useEffect(() => {
        if (activeFile) {
            setEditableContent(activeFile.content)
        }
    }, [activeFile])

    useEffect(() => {
        highlightCode()
        updateLineNumbers()
    }, [editableContent])

    const handleTabClick = (file) => {
        setActiveFile(file)
    }

    const handleCloseTab = (e, file) => {
        e.stopPropagation()
        closeFile(file)
    }

    const closeFile = (file) => {
        const newOpenFiles = openFiles.filter(f => f.name !== file.name)
        setOpenFiles(newOpenFiles)
        if (activeFile && activeFile.name === file.name) {
            const newActiveFile = newOpenFiles[0] || null
            setActiveFile(newActiveFile)
            setEditableContent(newActiveFile ? newActiveFile.content : '')
        }
        onCloseFile(file)
    }

    const handleContentChange = (e) => {
        const newContent = e.target.value
        setEditableContent(newContent)
        const updatedFile = { ...activeFile, content: newContent }
        onUpdateFile(updatedFile)
        setOpenFiles(openFiles.map(f => f.name === updatedFile.name ? updatedFile : f))

        // Get suggestions
        const cursorPos = e.target.selectionStart
        const currentWord = getCurrentWord(newContent, cursorPos)
        const language = getLanguage(activeFile.name)
        const newSuggestions = getSuggestions(currentWord, language)
        setCurrentSuggestions(newSuggestions)

        // Update cursor position for suggestions
        const textBeforeCursor = newContent.substring(0, cursorPos)
        const lines = textBeforeCursor.split('\n')
        const currentLineNumber = lines.length - 1
        const currentLineText = lines[currentLineNumber]
        const cursorLeft = getTextWidth(currentLineText, textareaRef.current)
        const cursorTop = currentLineNumber * parseFloat(getComputedStyle(textareaRef.current).lineHeight)
        setCursorPosition({ top: cursorTop, left: cursorLeft })
    }

    const getCurrentWord = (text, cursorPos) => {
        const textBeforeCursor = text.substring(0, cursorPos)
        const words = textBeforeCursor.split(/\s/)
        return words[words.length - 1]
    }

    const getSuggestions = (word, language) => {
        if (!word) return []
        return suggestions[language].filter(s => s.startsWith(word))
    }

    const getTextWidth = (text, element) => {
        const canvas = document.createElement('canvas')
        const context = canvas.getContext('2d')
        context.font = getComputedStyle(element).font
        return context.measureText(text).width
    }

    const handleSuggestionClick = (suggestion) => {
        const cursorPos = textareaRef.current.selectionStart
        const textBeforeCursor = editableContent.substring(0, cursorPos)
        const textAfterCursor = editableContent.substring(cursorPos)
        const words = textBeforeCursor.split(/\s/)
        const lastWord = words[words.length - 1]
        const newContent = textBeforeCursor.substring(0, textBeforeCursor.length - lastWord.length) + suggestion + textAfterCursor
        setEditableContent(newContent)
        setCurrentSuggestions([])
    }

    const highlightCode = () => {
        if (highlightedLinesRef.current && activeFile) {
            const language = getLanguage(activeFile.name)
            const highlightedCode = Prism.highlight(editableContent, Prism.languages[language], language)
            highlightedLinesRef.current.innerHTML = highlightedCode
        }
    }

    const updateLineNumbers = () => {
        if (lineNumbersRef.current) {
            const lineCount = editableContent.split('\n').length
            lineNumbersRef.current.innerHTML = Array(lineCount)
                .fill(0)
                .map((_, i) => `<div>${i + 1}</div>`)
                .join('')
        }
    }

    const handleScroll = (e) => {
        if (highlightedLinesRef.current && lineNumbersRef.current) {
            highlightedLinesRef.current.scrollTop = e.target.scrollTop
            lineNumbersRef.current.scrollTop = e.target.scrollTop
        }
    }

    const getLanguage = (fileName) => {
        const extension = fileName.split('.').pop().toLowerCase()
        switch (extension) {
            case 'js':
            case 'jsx':
                return 'javascript'
            case 'html':
                return 'html'
            case 'css':
                return 'css'
            case 'json':
                return 'json'
            case 'md':
                return 'markdown'
            default:
                return 'text'
        }
    }

    const getFileIcon = (fileName) => {
        const extension = fileName.split('.').pop().toLowerCase()
        switch (extension) {
            case 'js':
            case 'jsx':
                return '📄'
            case 'html':
                return '🌐'
            case 'css':
                return '🎨'
            case 'json':
                return '📊'
            case 'md':
                return '📝'
            default:
                return '📄'
        }
    }

    if (!activeFile) {
        return <div className="flex-grow bg-gray-900 text-gray-300 p-4">No file selected</div>
    }

    return (
        <div className="flex flex-col h-full w-4/5 bg-gray-900 text-gray-300">
            <div className="flex bg-gray-800 text-sm">
                {openFiles.map(file => (
                    <div
                        key={file.name}
                        className={`flex items-center px-3 py-1 cursor-pointer group ${activeFile.name === file.name ? 'bg-gray-900' : 'bg-gray-800 hover:bg-gray-700'}`}
                        onClick={() => handleTabClick(file)}
                    >
                        <span className="mr-1">{getFileIcon(file.name)}</span>
                        <span>{file.name}</span>
                        <X
                            size={14}
                            className="ml-2 opacity-0 group-hover:opacity-100 text-white"
                            onClick={(e) => handleCloseTab(e, file)}
                        />
                    </div>
                ))}
            </div>
            <div className="flex-grow overflow-hidden relative">
                <div className="absolute top-2 right-2 flex space-x-2 z-10">
                    <Play size={16} className="cursor-pointer" title="Run Code" />
                    <Split size={16} className="cursor-pointer" title="Split Editor" />
                    <Maximize2 size={16} className="cursor-pointer" title="Maximize Editor" />
                </div>
                <div className="flex h-full">
                    <div
                        ref={lineNumbersRef}
                        className="bg-gray-800 text-gray-500 p-4 text-right select-none overflow-hidden"
                        style={{
                            minWidth: '3em',
                            fontFamily: 'monospace',
                            fontSize: '14px',
                            lineHeight: '1.5',
                        }}
                    />
                    <div className="relative flex-grow overflow-hidden">
                        <pre
                            ref={highlightedLinesRef}
                            className="absolute top-0 left-0 w-full h-full m-0 p-4 overflow-hidden pointer-events-none"
                            style={{
                                fontFamily: 'monospace',
                                fontSize: '14px',
                                lineHeight: '1.5',
                                tabSize: 2,
                            }}
                        />
                        <textarea
                            ref={textareaRef}
                            value={editableContent}
                            onChange={handleContentChange}
                            onScroll={handleScroll}
                            className="absolute top-0 left-0 w-full h-full bg-transparent text-transparent caret-white p-4 outline-none resize-none z-10 overflow-auto"
                            style={{
                                fontFamily: 'monospace',
                                fontSize: '14px',
                                lineHeight: '1.5',
                                tabSize: 2,
                            }}
                        />
                        {currentSuggestions.length > 0 && (
                            <div
                                className="absolute bg-gray-700 text-white rounded-md shadow-lg z-20"
                                style={{
                                    top: `${cursorPosition.top + 40}px`,
                                    left: `${cursorPosition.left}px`,
                                }}
                            >
                                {currentSuggestions.map((suggestion, index) => (
                                    <div
                                        key={index}
                                        className="px-3 py-1 cursor-pointer hover:bg-gray-600"
                                        onClick={() => handleSuggestionClick(suggestion)}
                                    >
                                        {suggestion}
                                    </div>
                                ))}
                            </div>
                        )}
                    </div>
                </div>
            </div>
        </div>
    )
}