"use client"

import React, { useState } from 'react'
import Header from './Components/Header'
import Sidebar from './Components/Sidebar'
import Editor from './Components/Editor'

export default function App() {
  const [openFiles, setOpenFiles] = useState([])

  const handleFileSelect = (file) => {
    if (!openFiles.find(f => f.name === file.name)) {
      setOpenFiles([...openFiles, file])
    }
  }

  const handleCloseFile = (file) => {
    setOpenFiles(openFiles.filter(f => f.name !== file.name))
  }

  const handleUpdateFile = (updatedFile) => {
    setOpenFiles(openFiles.map(f => f.name === updatedFile.name ? updatedFile : f))
  }

  return (
    <div className="flex flex-col h-screen">
      <Header />
      <div className="flex flex-grow overflow-hidden">
        <Sidebar onFileSelect={handleFileSelect} />
        <Editor
          files={openFiles}
          onCloseFile={handleCloseFile}
          onUpdateFile={handleUpdateFile}
        />
      </div>
    </div>
  )
}