"use client"

import React, { useState } from 'react'
import { File, Folder, Search, GitBranch, ChevronDown, ChevronRight, Plus, RefreshCw, FolderPlus, Edit2 } from 'lucide-react'

const initialFileStructure = [
  {
    name: 'My Webpage',
    type: 'folder',
    children: [
      {
        name: 'index.html',
        type: 'file',
        content: `<!DOCTYPE html>
<html lang="en">
<head>
    <meta charset="UTF-8">
    <meta name="viewport" content="width=device-width, initial-scale=1.0">
    <title>My Webpage</title>
    <link rel="stylesheet" href="styles.css">
</head>
<body>
    <header>
        <h1>Welcome to My Webpage</h1>
        <nav>
            <ul>
                <li><a href="#about">About</a></li>
                <li><a href="#services">Services</a></li>
                <li><a href="#contact">Contact</a></li>
            </ul>
        </nav>
    </header>

    <main>
        <section id="about">
            <h2>About Us</h2>
            <p>This is a demo webpage created to showcase HTML structure.</p>
        </section>

        <section id="services">
            <h2>Our Services</h2>
            <ul>
                <li>Web Development</li>
                <li>Design</li>
                <li>Consulting</li>
            </ul>
        </section>

        <section id="contact">
            <h2>Contact Us</h2>
            <form>
                <label for="name">Name:</label>
                <input type="text" id="name" name="name" required>
                
                <label for="email">Email:</label>
                <input type="email" id="email" name="email" required>
                
                <button type="submit">Submit</button>
            </form>
        </section>
    </main>

    <footer>
        <p>&copy; 2024 My Webpage. All rights reserved.</p>
    </footer>

    <script src="script.js"></script>
</body>
</html>`,
      },
    ],
  },
];


export default function Sidebar({ onFileSelect }) {
  const [activeTab, setActiveTab] = useState('explorer')
  const [expandedFolders, setExpandedFolders] = useState(['OPEN EDITORS', 'REACT-VS-CODE', 'src'])
  const [fileStructure, setFileStructure] = useState(initialFileStructure)
  const [searchQuery, setSearchQuery] = useState('')
  const [searchResults, setSearchResults] = useState([])

  const toggleFolder = (folderName) => {
    setExpandedFolders(prev =>
      prev.includes(folderName)
        ? prev.filter(name => name !== folderName)
        : [...prev, folderName]
    )
  }

  const handleFileClick = (file) => {
    onFileSelect(file)
  }

  const addItem = (type, parentPath = '') => {
    const newName = prompt(`Enter the new ${type} name:`)
    if (newName) {
      const newFileStructure = [...fileStructure]
      const parent = parentPath ? findFolder(newFileStructure, parentPath) : { children: newFileStructure }
      if (parent) {
        parent.children.push({
          name: newName,
          type: type,
          children: type === 'folder' ? [] : undefined,
          content: type === 'file' ? '' : undefined
        })
        setFileStructure(newFileStructure)
      }
    }
  }

  const renameItem = (item, path) => {
    const newName = prompt('Enter the new name:', item.name)
    if (newName && newName !== item.name) {
      const newFileStructure = [...fileStructure]
      const parent = path.includes('/') ? findFolder(newFileStructure, path.split('/').slice(0, -1).join('/')) : { children: newFileStructure }
      if (parent) {
        const index = parent.children.findIndex(child => child.name === item.name)
        if (index !== -1) {
          parent.children[index].name = newName
          setFileStructure(newFileStructure)
        }
      }
    }
  }

  const findFolder = (structure, path) => {
    const pathParts = path.split('/')
    let current = { children: structure }
    for (const part of pathParts) {
      current = current.children.find(item => item.name === part)
      if (!current) return null
    }
    return current
  }

  const renderFileTree = (items, path = '') => {
    return items.map((item) => {
      const fullPath = path ? `${path}/${item.name}` : item.name
      if (item.type === 'folder' || item.type === 'section') {
        const isExpanded = expandedFolders.includes(item.name)
        return (
          <div key={fullPath}>
            <div
              className="flex items-center py-1 px-2 hover:bg-gray-800 cursor-pointer group"
              onClick={() => toggleFolder(item.name)}
            >
              {isExpanded ? <ChevronDown size={16} /> : <ChevronRight size={16} />}
              {item.type === 'folder' ? <Folder size={16} className="mr-1" /> : null}
              <span>{item.name}</span>
              {item.type === 'folder' && (
                <div className="ml-auto hidden group-hover:flex">
                  <Plus size={16} className="mr-1" onClick={(e) => { e.stopPropagation(); addItem('file', fullPath) }} />
                  <FolderPlus size={16} className="mr-1" onClick={(e) => { e.stopPropagation(); addItem('folder', fullPath) }} />
                  <Edit2 size={16} onClick={(e) => { e.stopPropagation(); renameItem(item, fullPath) }} />
                </div>
              )}
            </div>
            {isExpanded && item.children && (
              <div className="pl-4">
                {renderFileTree(item.children, fullPath)}
              </div>
            )}
          </div>
        )
      } else {
        return (
          <div
            key={fullPath}
            className="flex items-center py-1 px-2 hover:bg-gray-800 cursor-pointer group"
            onClick={() => handleFileClick(item)}
          >
            <File size={16} className="mr-1" />
            <span>{item.name}</span>
            <div className="ml-auto hidden group-hover:flex">
              <Edit2 size={16} onClick={(e) => { e.stopPropagation(); renameItem(item, fullPath) }} />
            </div>
          </div>
        )
      }
    })
  }

  const handleSearch = (query) => {
    setSearchQuery(query)
    if (query) {
      const results = searchFiles(fileStructure, query)
      setSearchResults(results)
    } else {
      setSearchResults([])
    }
  }

  const searchFiles = (items, query, path = '') => {
    let results = []
    for (const item of items) {
      const fullPath = path ? `${path}/${item.name}` : item.name
      if (item.type === 'file' && item.name.toLowerCase().includes(query.toLowerCase())) {
        results.push({ ...item, path: fullPath })
      }
      if (item.children) {
        results = results.concat(searchFiles(item.children, query, fullPath))
      }
    }
    return results
  }

  return (
    <div className="w-1/5 bg-gray-900 text-gray-300 flex flex-col h-full">
      <div className="flex">
        <button
          className={`flex-1 p-2 ${activeTab === 'explorer' ? 'bg-gray-800' : ''}`}
          onClick={() => setActiveTab('explorer')}
        >
          <File size={20} />
        </button>
        <button
          className={`flex-1 p-2 ${activeTab === 'search' ? 'bg-gray-800' : ''}`}
          onClick={() => setActiveTab('search')}
        >
          <Search size={20} />
        </button>
        <button
          className={`flex-1 p-2 ${activeTab === 'github' ? 'bg-gray-800' : ''}`}
          onClick={() => setActiveTab('github')}
        >
          <GitBranch size={20} />
        </button>
      </div>
      {activeTab === 'explorer' && (
        <div className="overflow-auto flex-grow">
          <div className="flex justify-between items-center p-2 border-b border-gray-700">
            <span>EXPLORER</span>
            <div className="flex space-x-2">
              <button onClick={() => addItem('file')}><Plus size={16} /></button>
              <button onClick={() => addItem('folder')}><FolderPlus size={16} /></button>
              <button onClick={() => setFileStructure([...fileStructure])}><RefreshCw size={16} /></button>
            </div>
          </div>
          {renderFileTree(fileStructure)}
        </div>
      )}
      {activeTab === 'search' && (
        <div className="p-2">
          <input
            type="text"
            placeholder="Search"
            className="w-full bg-gray-800 text-white p-2 rounded"
            value={searchQuery}
            onChange={(e) => handleSearch(e.target.value)}
          />
          <div className="mt-2">
            {searchResults.map((file, index) => (
              <div
                key={index}
                className="flex items-center py-1 px-2 hover:bg-gray-800 cursor-pointer"
                onClick={() => handleFileClick(file)}
              >
                <File size={16} className="mr-1" />
                <span>{file.path}</span>
              </div>
            ))}
          </div>
        </div>
      )}
      {activeTab === 'github' && (
        <div className="p-2">
          <p>GitHub integration placeholder</p>
        </div>
      )}
    </div>
  )
}